/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Kumbh Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  color: hsl(229deg, 23%, 23%);
  text-align: center;
}

body {
  background-color: hsl(185deg, 75%, 39%);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.background {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  overflow: hidden;
}
.background #top-pattern {
  position: absolute;
  transform: translate(-45%, -50%);
}
.background #bottom-pattern {
  position: absolute;
  transform: translate(45%, 50%);
}

main {
  width: calc(100% - 50px);
  z-index: 1;
  max-width: 350px;
  min-width: 250px;
  box-shadow: 0 20px 20px 10px hsla(0deg, 0%, 0%, 0.1);
}
main .hero {
  width: 100%;
  height: 140px;
  background-image: url(../img/bg-pattern-card.svg);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: relative;
}
main .hero .profile-picture {
  position: absolute;
  bottom: 0;
  transform: translate(-50%, 50%);
  border-radius: 50%;
  border: 5px solid hsl(0deg, 0%, 95%);
}
main .informations-wrapper {
  background-color: hsl(0deg, 0%, 95%);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
main .informations-wrapper h1 {
  font-size: 1rem;
  font-weight: 700;
  padding-top: 4rem;
  margin-bottom: 0.4rem;
}
main .informations-wrapper h1 span {
  margin-left: 0.3rem;
  color: hsl(227deg, 10%, 56%);
}
main .informations-wrapper .location {
  color: hsl(227deg, 10%, 56%);
  font-size: 0.8rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
main .informations-wrapper .stats-container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 1.3rem 2rem;
  border-top: 1px solid hsl(0deg, 0%, 83%);
}
main .informations-wrapper .stats-container .stat-item {
  flex: 1;
}
main .informations-wrapper .stats-container .stat-item .number {
  font-weight: 700;
  margin-bottom: 0.2rem;
}
main .informations-wrapper .stats-container .stat-item .topic {
  font-size: 0.6rem;
  letter-spacing: 0.08rem;
}